// Screen
.screen {
  border: 3px solid var(--theme-secondary);
  background-color: white;
  margin: 8px;
  height: 78vh;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  .column {
    border: 1px solid var(--theme-secondary);
    padding: 24px;
  }
  .map {
    background-color: var(--subtheme-2);
    .grid {
      background-color: white;
    }
    .player-info {
      display: flex;
      padding: 10px;
      justify-content: space-between;
      font-size: 1.2rem;
      color: darkslategray;
    }
    .seed-info {
      background-color: var(--theme-primary);
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      gap: 3px;
      .loot-title {
        font-weight: bold;
        font-size: 1.5rem;
        color: white;
      }
      .seed-slots {
        display: flex;
        gap: 1rem;
        .seed-item {
          background-color: white;
          font-size: 24px;
          font-weight: bold;
          color: var(--theme-secondary);
        }
      }
    }
  }
  .log {
    background-color: var(--subtheme-1);
    .log-display {
      display: flex;
      flex-direction: column;
      background-color: white;
      max-height: 60vh;
      .log-entry {
        color: var(--theme-primary);
        font-size: 1.2rem;
        .turn {
          font-size: 1.2rem;
          color: var(--grey-2);
        }
      }
    }
  }
}

// Equipment
.equipment {
  background-color: var(--subtheme-1);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  .equipment-slots {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    justify-content: space-evenly;
    align-items: center;
    .equipment-item {
      border: 3px solid var(--theme-secondary);
      border-radius: 16px;
      width: 8rem;
      height: 8rem;
      padding: 12px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: white;
      .consumed {
        border: 3px solid var(--theme-secondary);
        border-radius: 16px;
        width: 8rem;
        height: 8rem;
        padding: 12px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: black;
      }
      .equipment-slot-item {
        background-color: white;
        border: none;
        .equipment-image {
          height: 5rem;
        }
        &:hover {
          cursor: pointer;
        }
        .tooltip {
          visibility: hidden;
          max-width: 12rem;
          background-color: black;
          color: #fff;
          text-align: center;
          border-radius: 6px;
          padding: 5px;
          display: flex;
          flex-direction: column;
        
          /* Position the tooltip */
          position: absolute;
          z-index: 1;
        }
        &:hover {
          cursor: pointer;
          background-color: white;
          color: black;
          .tooltip {
            visibility: visible;
          }
        }
      }
      .options-popup {
        font-size: 1.5rem;
        color: white;
        position: fixed;
        top: 20%;
        left: 30%;
        display: flex;
        justify-content: center;
        align-items: center;
        z-index: 2;
        .plant-options {
          padding: 2rem;
          padding-bottom: 4rem;
          margin-left: 7rem;
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 16px;
          background-color: var(--theme-primary);
          border: 3px solid var(--theme-secondary);
          .plant-grid {
            max-width: 50rem;
            display: grid;
            grid-template-columns: repeat(5, 1fr);
            gap: 2rem;
            .inventory-slot {
              border: 3px solid var(--theme-secondary);
              width: 4.5rem;
              height: 4.5rem;
              border-radius: 16px;
              background-color: slategray;
              color: white;
              font-size: 16px;
              display: flex;
              flex-direction: column;
              justify-content: center;
              align-items: center;
              transition: 0.2s;
              transition-timing-function: ease-in-out;
            }
            .plant {
              border: 3px solid var(--theme-secondary);
            }
            .item {
              background-color: white;
              color: var(--theme-secondary);
              .item-name {
                font-size: 1.2rem;
              }
              .Common {
                color: yellow;
              }
              .Uncommon {
                color: greenyellow;
              }
              .Rare {
                color: cyan;
              }
              .Mythical {
                color: violet;
              }
              .tooltip {
                visibility: hidden;
                max-width: 12rem;
                background-color: black;
                color: #fff;
                text-align: center;
                border-radius: 6px;
                padding: 5px;
                display: flex;
                flex-direction: column;
              
                /* Position the tooltip */
                position: absolute;
                top: 80%;
                z-index: 1;
              }
              &:hover {
                cursor: pointer;
                background-color: var(--subtheme-2);
                color: black;
                .tooltip {
                  visibility: visible;
                }
              }
            }
          }
        }
      }
    }
  }
}

.close-btn {
  position: absolute;
  top: 16px;
  right: 16px;
  background-color: darkslategray;
  color: white;
  font-weight: bold;
  &:hover {
    cursor: pointer;
    background-color: lightslategray;
    color: black;
  }
}

// Map
.map {
  display: flex;
  flex-direction: column;
  .seed-info {
    border: 3px solid var(--theme-secondary);
    border-radius: 16px;
    padding: 10px;
    display: flex;
    justify-content: center;
    gap: 2rem;
    .seed-item {
      border: 3px solid var(--theme-secondary);
      border-radius: 16px;
      width: 1.5rem;
      height: 1.5rem;
      padding: 12px;
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
}

// Grid
.grid {
  display: flex;
  flex-direction: column;
  border: 3px solid var(--theme-secondary);
  padding: 16px;
  justify-content: center;
  align-items: center;
  height: 25rem;
  gap: 1px;
  .grid-row {
    display: flex;
    flex-direction: row;
    gap: 1px;
    height: 100%;
    width: 50%;
    .grid-cell {
      border: 3px solid darkslategray;
      width: 100%;
      height: 100%;
      min-width: 2.5rem;
      min-height: 2.5rem;
      border-radius: 8px;
      background-color: slategray;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 100%;
      transition: 0.2s;
      transition-timing-function: ease-in-out;
    }
    .closed {
      &:hover {
        background-color: lightgrey;
        cursor: pointer;
      }
    }
    .scanned {
      background-color: goldenrod;
      border: 3px solid darkgoldenrod;
      &:hover {
        background-color: lightgoldenrodyellow;
        cursor: pointer;
      }
    }
    // Cell subclass
    .empty {
      background-color: brown;
    }
    .loot {
      background-color: white;
    }
  }
}

// Log
.log-display {
  border: 3px solid black;
  padding: 8px;
  height: 85%;
  overflow-y: scroll;
}